.flexbox {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.content-box {
  @media screen and (max-width: 767.98px) {
    padding-bottom: 50px;
  }
}

.program__title {
  font-family: var(--decorative-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 2.71;
  text-align: center;
  margin: 0;

  @media screen and (min-width: 768px) {
    text-align: left;
  }

  @media screen and (min-width: 1280px) {
    font-size: 36px;
    line-height: 1.8;
  }
}

.program__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;

  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.22;
  }
}

.program__text--mb {
  margin: 0;
  margin-bottom: 10px;

  @media screen and (min-width: 1280px) {
    margin-bottom: 21px;
  }
}

.program__list {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;

  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.22;
  }
}

.program__item {
  margin-bottom: 10px;

  @media screen and (min-width: 1280px) {
    margin-bottom: 15px;
  }
}

.program__item::before {
   content: "\003E";
}

.read-more {
  display: none;
}
.read-more.is-open {
  display: inline;
}

@media screen and (min-width: 1280px) {
  .read-more {
    display: inline;
  }
}

.program__button {
  display: block;
  border: none;
  cursor: pointer;
  padding: 5px 0;
  font-family: var(--decorative-font);
  font-weight: 400;
  font-size: 18px;
  line-height: 1.18;
  color: var(--main-accent);
  background-color: transparent;

  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.program__button .close {
  display: none;
}

.program__button.is-open .close {
  display: block;
}

.program__button.is-open .open {
  display: none;
}

.open,
.close {
   margin-bottom: 0;
}

.program__item--details:nth-child(odd) {
   @media screen and (min-width: 320px) {
      transform: translateY(-20px);
   }
