.program__item--details {
   @media screen and (max-width: 319.98px) {
      &:not(:first-child) {
         margin-top: 55px;
      }
   }
   @media screen and (min-width: 320px) {
      &:nth-child(3),
      &:nth-child(4) {
         margin-top: 55px;
      }
   }
}

.content-box {
   flex-basis: calc((100% - 38px) / 2);
}

.grid {
   display: flex;
   align-items: center;
   justify-content: space-between;

   @media screen and (max-width: 319.98px) {
      flex-direction: column;
   }

   @media screen and (min-width: 320px) {
      flex-wrap: wrap;
   }

   @media screen and (min-width: 768px) {
      flex-basis: calc((100% - 38px) / 2);
   }

   @media screen and (max-width: 767.98px) {
      max-width: 320px;
      margin-left: auto;
      margin-right: auto;
   }
}

.program__item--details {
   width: 142px;
   height: 142px;
   background: #fef3e4;
   box-shadow: 0px 4px 4px rgba(171, 129, 108, 0.25);
   border-radius: 50%;
   padding: 12px 17px;
   text-align: center;
   display: flex;
   flex-direction: column;
   justify-content: flex-end;
   align-items: center;

   @media screen and (min-width: 1280px) {
      width: 180px;
      height: 180px;
   }
}
