.logo {
  @media screen and (max-width: 767.98px) {
    text-align: center;
    margin: auto;
    width: 113px;
    height: 48px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 96px;
    height: 43px;
    text-align: left;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  @media screen and (min-width: 1280px) {
    width: 158px;
    height: 71px;
  }
}
